  width: 115px;
  height:115px;
  border-color:#172430;
  background:#172430;
  text-align: center;
  padding: 8px;
  color: gray; 
}

.runeBtn2 {
  border-radius:10px;
  width: 115px;
  height:115px;
  border-color:#172430;
  background:#21bf96;
  text-align: center;
  padding: 8px;
}

.runeBtn:hover {
